.close {
  float: right;
  font-size: 1.3125rem;
  font-weight: 700;
  line-height: 1;
  color: $black;
  text-shadow: 0 1px 0 $white;
  opacity: .8;

  &:hover {
    color: $black;
    text-decoration: none;
  }

  &:not(:disabled):not(.disabled) {
    &:hover, &:focus {
      opacity: .75;
    }
  }
}

